(function () {
  const sliders = document.querySelectorAll(`.gallery__container`);

  if (sliders) {

    sliders.forEach(function () {
      const options = {
        loop: true,
        slidesPerView: `auto`,
        spaceBetween: 0,
        speed: 400,

        // Navigation arrows
        navigation: {
          nextEl: `.gallery__arrow--next`,
          prevEl: `.gallery__arrow--prev`,
        },
      };

      const slider = new Swiper(`.gallery__container`, options);
    });
  }
})();
