
document.addEventListener(`DOMContentLoaded`, () => {
  const mobileOverlay = document.querySelector(`.mobile-overlay`);
  const body = document.querySelector(`.index`);
  const SLIDER_HEIGHT = 860;
  const SLIDER_WIDTH = 1280;
  const screenWidth = screen.width > screen.height ? screen.width : screen.height;
  const screenHeight = screen.width < screen.height ? screen.width : screen.height;

  if (mobileOverlay && body) {
    const onWindowResize = function () {
      if (getComputedStyle(mobileOverlay)[`display`] === `none` && screenWidth < SLIDER_WIDTH) {
        const scaleEffector = screenWidth / SLIDER_WIDTH;
        body.style.transform = `scale(${screenHeight / (SLIDER_HEIGHT * scaleEffector)})`;
      } else {
        body.style.transform = ``;
      }
    };

    window.addEventListener(`resize`, onWindowResize);

    onWindowResize();
  }
});

